import React from 'react';
import { RegisterAndCreateLeagueForm } from '../components/LeagueForm/RegisterAndCreateLeagueForm';
import LoginBg from '../assets/login-bg.svg';
import { ThemeNavbarLayout } from '../components/theme/ThemeNavbarLayout';

const Index = () => {
    return (
        <ThemeNavbarLayout>
            <div className="flex">
                <div className="hidden w-1/2 h-full lg:block">
                    <img
                        src={LoginBg}
                        alt="login-bg"
                        className="fixed left-0 object-cover w-1/2 h-full min-h-screen"
                    />
                </div>
                <div className="w-1/2 pb-0">
                    <RegisterAndCreateLeagueForm />
                </div>
            </div>
        </ThemeNavbarLayout>
    );
};

export default Index;
