import React from 'react';
import { APP_ROUTES } from '../../constants';
import { navigate } from 'gatsby';
import { useLeagueStore, LeagueStore } from '../../store/league';
import { useUserStore, UserState } from '../../store/user';
import { LeagueForm, LeagueFormState } from './index';

export const RegisterAndCreateLeagueForm: React.FC<{ standalone?: boolean }> = ({ standalone }) => {
    const [localAlert, setLocalAlert] = React.useState<string>('');
    const { user, createUserWithEmailAndPassword, sendVerificationEmail } = useUserStore(
        (state: UserState) => ({
            user: state.user,
            createUserWithEmailAndPassword: state.createUserWithEmailAndPassword,
            sendVerificationEmail: state.sendVerificationEmail,
        })
    );

    const { createLeague } = useLeagueStore((state: LeagueStore) => {
        return {
            createLeague: state.createLeague,
        };
    });

    async function handleSubmit(formState: LeagueFormState) {
        try {
            if (!user && formState.email && formState.password && formState.name) {
                await createUserWithEmailAndPassword(
                    formState.email,
                    formState.password,
                    formState.name
                );
                await sendVerificationEmail();
            }
            await createLeague({ ...formState, userId: user?.uid });
            navigate(
                `${APP_ROUTES.THANK_YOU}?platform=${formState.platform}&leagueid=${formState.leagueid}`
            );
        } catch (err) {
            setLocalAlert(err.message);
        }
    }

    return (
        <LeagueForm
            title="League Information"
            onSubmit={handleSubmit}
            localAlertText={localAlert}
            showDiscordButton
            sendNotification
            standalone={standalone}
        />
    );
};
